import React from "react";
import { FaIcon, FacetIcon } from "../graphics/FontAwesomeIcons";
import { DropdownButton, Dropdown } from "react-bootstrap";
import SearchIconList from "./SearchIconList";

const searchItemMenuList = [
  { name: "Extract Entities", href: "#", iconIndex: 9, className: "" },
  {
    name: "Send Extracted Entities to a Workbook",
    href: "#",
    iconIndex: 10,
    className: "",
  },
  {
    name: "Send Document to a Workbook",
    href: "#",
    iconIndex: 11,
    className: "",
  },
  { name: "Download Document", href: "#", iconIndex: 12, className: "" },
];
/**
 * @function searchItemMenuPropsList Builds the flag, favorite, and hidden menu item
 * based on the properties passed to the function
 * @param {*} props The properties of flagged, favorite, and hidden are passed
 * @returns
 */
const searchItemMenuPropsList = (props) => {
  // Build the flag/unflag menu
  const flagMenuObj = {
    name:
      props.flagged === "true" || props.flagged === true
        ? "Remove Flag on Document"
        : "Flag Document",
    href: "#",
    iconIndex: props.flagged === true ? 13 : 14,
    className: "",
  };
  // Build the favorite/remove favorite menu
  const favoriteMenuObj = {
    name:
      props.favorite === "true" || props.favorite === true
        ? "Remove as Favorite"
        : "Add as Favorite",
    iconIndex: props.favorite === true ? 15 : 16,
    href: "#",
    className: "",
  };
  // Build the hide/unhide menu
  const hiddenMenuObj = {
    name:
      props.hidden === "true" || props.hidden === true
        ? "Unhide Document"
        : "Hide Document",
    href: "#",
    iconIndex: props.hidden === true ? 17 : 18,
    className: "",
  };

  // Return array of data to build dropdowns for falgging, favorite, and hidden
  return [flagMenuObj, favoriteMenuObj, hiddenMenuObj];
};

const SearchItemMenu = (props) => {
  return (
    <DropdownButton
      variant="outline-secondary nocaret"
      title={FaIcon("fas", "ellipsis-h", "Search Item Options Menu")}
      id="searchOptionsMenu"
      align="end"
    >
      {/** Build the New combined array by adding a spread and combining into new array */}
      {[...searchItemMenuList, ...searchItemMenuPropsList(props)].map(
        (item, index) => {
          return (
            <Dropdown.Item
              href={item.href}
              eventKey={index + 1}
              className={item.className}
              key={index}
            >
              <FacetIcon iconIndex={item.iconIndex} iconList={SearchIconList} />{" "}
              {item.name}
            </Dropdown.Item>
          );
        }
      )}
    </DropdownButton>
  );
};
export default SearchItemMenu;
