import React from "react";
import { FaIcon } from "../graphics/FontAwesomeIcons";
import { ButtonGroup, DropdownButton, Dropdown, Button } from "react-bootstrap";

const userMenuList = [
  {
    name: "Edit User Profile",
    iconFam: "fas",
    iconName: "user-edit",
    link: "",
  },
  { name: "Manage Data", iconFam: "fas", iconName: "user-lock", link: "" },
  { name: "Change Password", iconFam: "fas", iconName: "user-cog", link: "" },
];

const UserMenu = (props) => {
  return (
    <ButtonGroup bg="outline-secondary">
      <DropdownButton
        variant="outline-secondary"
        className="nocaret"
        drop="start"
        as={ButtonGroup}
        title={FaIcon("fas", "user", "User Profile Menu")}
        id="bg-nested-dropdown"
      >
        {userMenuList.map((item, index) => {
          return (
            <Dropdown.Item eventKey={index + 1} key={index}>
              {FaIcon(item.iconFam, item.iconName)} {item.name}
            </Dropdown.Item>
          );
        })}
      </DropdownButton>
      <Button variant="outline-secondary" title="Sign Out">
        {FaIcon("fas", "sign-out-alt")}
      </Button>
    </ButtonGroup>
  );
};
export default UserMenu;
