import React from "react";
import { FaIcon, FacetIcon } from "../graphics/FontAwesomeIcons";
import { InputGroup, DropdownButton, Dropdown, Form } from "react-bootstrap";
import WorkspaceIconList from "./WorkspaceIconList";

/* This will be replaced with a list that comes from an API call */
const workspacesList = [
  { name: "Alpha One", value: "1" },
  { name: "Bravo Two", value: "2" },
  { name: "Charlie Three", value: "3" },
];
/* this builds the workspace menu list */
const workspaceMenuList = [
  { name: "Workspace Properties", onClick: "#", iconIndex: 0 },
  { name: "Add New Workspace", onClick: "#", iconIndex: 1 },
  { name: "Manage Workspaces", onClick: "#", iconIndex: 2 },
  { name: "Clone Workspace", onClick: "#", iconIndex: 3 },
];
/**
 * @function WorkspaceMenu
 * @param {*} props passed properties from parent components
 * @returns Workspace Main menu
 */
const WorkspaceMenu = (props) => {
  return (
    <InputGroup className="">
      <Form.Select
        id="workspaceSelection"
        size="sm"
        aria-label="Default select example"
      >
        {workspacesList.map((item, index) => {
          return (
            <option value={index + 1} key={index}>
              {item.name}
            </option>
          );
        })}
      </Form.Select>

      <DropdownButton
        variant="outline-secondary nocaret"
        title={FaIcon("fas", "ellipsis-h", "Workspace Options Menu")}
        id="workspace-main-dropdown"
        align="end"
        onHover={() => {
          alert("hovered");
        }}
      >
        {workspaceMenuList.map((item, index) => {
          return (
            <Dropdown.Item
              eventKey={index + 1}
              onClick={item.onClick}
              key={index}
            >
              <FacetIcon
                iconIndex={item.iconIndex}
                iconList={WorkspaceIconList}
              />{" "}
              {item.name}
            </Dropdown.Item>
          );
        })}
      </DropdownButton>
    </InputGroup>
  );
};
export default WorkspaceMenu;
