/**
 * @function ArrayIndexMove
 * @param {*} arr The array being passed to the function for index reassign
 * @param {*} oldIndex The index of the item you want moved
 * @param {*} newIndex The new index where you want to move the item
 * @returns array with item moved from the old index to the new index
 */
const ArrayIndexMove = (arr, oldIndex, newIndex) => {
  if (newIndex >= arr.length) {
    var k = newIndex - arr.length + 1;
    while (k--) {
      arr.push(undefined);
    }
  }
  arr.splice(newIndex, 0, arr.splice(oldIndex, 1)[0]);
  return arr;
};
export default ArrayIndexMove;
