import React from "react";
// Import the Font Awesome libraries
import { dom, library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { far } from "@fortawesome/free-regular-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";

/**
 * @function Function return Font Awesome Icon for use in React title attribute
 * @param {*} family
 * @param {*} icon
 * @returns
 */
export const FaIcon = (family, icon, title) => (
  <FontAwesomeIcon icon={[family, icon]} title={title} />
);

/**
 * @function FacetIcon Function used to build masked, layered and Layered/Masked icons for FACET
 * @param {Number} iconIndex index from the list passed to the function
 * @param {Array} iconList Array list of icons to be built from
 * @returns
 */
export const FacetIcon = (props) => {
  if (typeof props.iconIndex === undefined || props.iconIndex === null) {
    console.error("Index must be provided for FacetIcon function.");
    return false;
  }
  if (typeof props.iconList === undefined || props.iconList === null) {
    console.error("An array of icons must be provided for FacetIcon function.");
    return false;
  }
  if (typeof props.iconList[props.iconIndex].type !== undefined) {
    if (props.iconList[props.iconIndex].type === "layer") {
      return (
        <span className="fa-layers fa-fw">
          <FontAwesomeIcon
            icon={[
              props.iconList[props.iconIndex].icon1Fam,
              props.iconList[props.iconIndex].icon1,
            ]}
            transform={props.iconList[props.iconIndex].icon1Transform}
            title={typeof props.title === undefined ? "" : props.title}
          />
          <FontAwesomeIcon
            icon={[
              props.iconList[props.iconIndex].icon2Fam,
              props.iconList[props.iconIndex].icon2,
            ]}
            transform={props.iconList[props.iconIndex].icon2Transform}
            title={typeof props.title === undefined ? "" : props.title}
          />
        </span>
      );
    } else if (props.iconList[props.iconIndex].type === "regular") {
      return (
        <FontAwesomeIcon
          icon={[
            props.iconList[props.iconIndex].icon1Fam,
            props.iconList[props.iconIndex].icon1,
          ]}
          title={typeof props.title === undefined ? "" : props.title}
        />
      );
    } else if (props.iconList[props.iconIndex].type === "mask") {
      return (
        <FontAwesomeIcon
          icon={[
            props.iconList[props.iconIndex].icon1Fam,
            props.iconList[props.iconIndex].icon1,
          ]}
          mask={[
            props.iconList[props.iconIndex].maskFam,
            props.iconList[props.iconIndex].maskIcon,
          ]}
          transform={props.iconList[props.iconIndex].icon1Transform}
          title={typeof props.title === undefined ? "" : props.title}
        />
      );
    } else if (props.iconList[props.iconIndex].type === "layer-mask") {
      return (
        <span
          className="fa-layers fa-fw"
          title={typeof props.title === undefined ? "" : props.title}
        >
          <FontAwesomeIcon
            icon={[
              props.iconList[props.iconIndex].icon1Fam,
              props.iconList[props.iconIndex].icon1,
            ]}
            mask={[
              props.iconList[props.iconIndex].maskFam,
              props.iconList[props.iconIndex].maskIcon,
            ]}
            transform={props.iconList[props.iconIndex].icon1Transform}
          />
          <FontAwesomeIcon
            icon={[
              props.iconList[props.iconIndex].icon2Fam,
              props.iconList[props.iconIndex].icon2,
            ]}
            transform={props.iconList[props.iconIndex].icon2Transform}
          />
        </span>
      );
    } else if (props.iconList[props.iconIndex].type === "document") {
      return (
        <span
          className="text-primary fa-2x fa-layers fa-fw"
          title={typeof props.title === undefined ? "" : props.title}
        >
          <FontAwesomeIcon
            icon={[
              props.iconList[props.iconIndex].icon1Fam,
              props.iconList[props.iconIndex].icon1,
            ]}
            mask={[
              props.iconList[props.iconIndex].maskFam,
              props.iconList[props.iconIndex].maskIcon,
            ]}
            transform={props.iconList[props.iconIndex].icon1Transform}
          />
          {props.iconList[props.iconIndex].icon2 !== "" ? (
            <FontAwesomeIcon
              icon={[
                props.iconList[props.iconIndex].icon2Fam,
                props.iconList[props.iconIndex].icon2,
              ]}
              transform={props.iconList[props.iconIndex].icon1Transform}
            />
          ) : null}
        </span>
      );
    }
  }
};

// Add the libraries to the application
library.add(fas);
library.add(far);
library.add(fab);

dom.i2svg();
