import React, { useState } from "react";
import { Button, Collapse } from "react-bootstrap";
import { FaIcon } from "../graphics/FontAwesomeIcons";

const ContentCollapse = (props) => {
  const [open, setOpen] = useState(props.open);

  return (
    <>
      <Button
        onClick={() => setOpen(!open)}
        // eslint-disable-next-line jsx-a11y/aria-props
        aria-controls={props.id + "-collapse-text"}
        aria-expanded={open}
        id={props.id + "-btn"}
        variant="link"
        size="sm"
        className="expander"
      >
        {FaIcon(
          "fas",
          open === true ? "chevron-down" : "chevron-right",
          "Toggle Close"
        )}{" "}
        {open === true ? "Click to Collapse" : "Click to Expand"}
      </Button>
      <Collapse in={open}>
        <div id={props.id + "-collapse-text"}>{props.children}</div>
      </Collapse>
    </>
  );
};
export default ContentCollapse;
