import React from "react";
import { FaIcon } from "../graphics/FontAwesomeIcons";
import { DropdownButton, Dropdown } from "react-bootstrap";
/**
 * @function WidthMenu
 * @param {*} props id, onWidthChange, sizes, section, idSuffix
 * @returns Width selection menu for workspace and workbook
 */
const WidthMenu = (props) => {
  const widthHandler = (item) => {
    let value = parseInt(item.target.id.split("_")[0]);
    props.onWidthChange(value);
  };
  return (
    <DropdownButton
      id={props.id}
      variant="outline-secondary nocaret"
      title={FaIcon(
        "fas",
        "arrows-alt-h",
        "Change " + props.section + " Area Width"
      )}
    >
      {props.sizes.map((item, index) => {
        if (item.selected === true) {
          return (
            <Dropdown.Item
              eventKey={index + 1}
              onClick={widthHandler}
              key={index}
              id={item.id + "_" + props.idSuffix}
            >
              <span className="text-success">
                {FaIcon("fas", "check", "Checked")}{" "}
              </span>
              {item[props.node]}
            </Dropdown.Item>
          );
        } else {
          return (
            <Dropdown.Item
              eventKey={index + 1}
              onClick={widthHandler}
              key={index}
              id={item.id + "_" + props.idSuffix}
            >
              <span className="text-white">
                {FaIcon("fas", "check", "NotChecked")}{" "}
              </span>
              {item[props.node]}
            </Dropdown.Item>
          );
        }
      })}
    </DropdownButton>
  );
};
export default WidthMenu;
