import React from "react";
import { FacetIcon } from "../graphics/FontAwesomeIcons";
import SearchIconList from "./SearchIconList";

/**
 * @function idicatorIconList Builds the flag, favorite, and hidden menu item
 * based on the properties passed to the function
 * @param {*} props The properties of flagged, favorite, and hidden are passed
 * @returns
 */
const idicatorIconList = (props) => {
  const indicatorArray = [];
  // Build the flag/unflag menu
  indicatorArray[0] = {
    name:
      props.flagged === "true" || props.flagged === true
        ? "Flagged Document"
        : "Unflagged Document",
    iconIndex: props.flagged === true ? 14 : 13,
  };
  // Build the favorite/remove favorite menu
  indicatorArray[1] = {
    name:
      props.favorite === "true" || props.favorite === true
        ? "Favorite"
        : "Not a Favorite",
    iconIndex: props.favorite === true ? 16 : 15,
  };
  // Build the hide/unhide menu
  indicatorArray[2] = {
    name:
      props.hidden === "true" || props.hidden === true
        ? "Hidden Document"
        : "Visible Document",
    iconIndex: props.hidden === true ? 18 : 17,
  };

  // Return array of data to build dropdowns for falgging, favorite, and hidden
  return indicatorArray;
};

const SearchIndicatorIcons = (props) => {
  return (
    <>
      {idicatorIconList(props).map((item, index) => {
        return (
          <span className="indicator" key={index}>
            <FacetIcon
              iconIndex={item.iconIndex}
              iconList={SearchIconList}
              key={index}
              title={item.name}
            />
          </span>
        );
      })}{" "}
    </>
  );
};
export default SearchIndicatorIcons;
