import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import WorkspaceArea from "../workspace/WorkspaceArea";
import WorkbookArea from "../workbook/WorkbookArea";
import { WorkAreaSizes, WorkspaceSizes } from "./WorkAreaSizes";
/**
 * @function ContentArea
 * @param {*} props
 * @returns the workspace and workbook areas.
 */
const ContentArea = (props) => {
  // Set state for workspace sizes object array
  const [workspaceSize, setWorkspaceSize] = useState(WorkspaceSizes());
  // Set state for workbook sizes object array
  const [workbookSize, setWorkbookSize] = useState(WorkAreaSizes);
  // Set a state for the workspace width
  const [workspaceWidth, setWorkspaceWidth] = useState(
    WorkAreaSizes[1].workspace
  );
  // Set a state for the workbook width
  const [workbookWidth, setWorkbookWidth] = useState(WorkAreaSizes[1].workbook);
  // Set a state for class names to be added in case of 0 width on workspace
  const [workspaceClass, setWorkspaceClass] = useState("");
  // Set a state for class names to be added in case of 0 width on workspace
  const [workbookClass, setWorkbookClass] = useState("");
  /**
   * @function zeroWidthClass
   * @param {*} value ID/index of the workbook array to set the hidden
   * class on columns with 0 width
   */
  const zeroWidthClass = (value) => {
    if (workbookSize[value].workspace === 0) {
      setWorkspaceClass("visually-hidden-focusable");
    } else {
      setWorkspaceClass("");
    }
    if (workbookSize[value].workbook === 0) {
      setWorkbookClass("visually-hidden-focusable");
    } else {
      setWorkbookClass("");
    }
  };
  /**
   * @function workAreaWidthHandler Used to reset the width state of workspace and workbook
   * @param {*} value the ID of the object in the array that is being selected
   */
  const workAreaWidthHandler = (value) => {
    // In order to not write the same loop twice, put actions and arrays
    // into a list to pass to the setMenuItems function
    const widthMenuList = [
      { list: workspaceSize, action: setWorkspaceSize },
      { list: workbookSize, action: setWorkbookSize },
    ];
    const setMenuItems = (list, action) => {
      // loop over the menu sizes list and find the provided id value.
      let updatedList = list.map((item) => {
        if (item.id === value) {
          zeroWidthClass(value);
          return { ...item, selected: true }; //updates the current selection to true
        } else if (item.id !== value) {
          return { ...item, selected: false }; //updates the other options to selected = false
        }
        return item; // else return unmodified item
      });
      // Update the state for the list
      action(updatedList);
    };
    // Loop over the 2 menu arrays and set action states
    for (let i = 0; i < widthMenuList.length; i++) {
      setMenuItems(widthMenuList[i].list, widthMenuList[i].action);
    }
    // Set the width value on the columns
    setWorkspaceWidth(workbookSize[value].workspace);
    setWorkbookWidth(workbookSize[value].workbook);
  };
  return (
    <Container fluid>
      <Row>
        <Col
          id="WorkspacePanel"
          md={workspaceWidth}
          sm={12}
          className={workspaceClass}
        >
          {/** Start Workspace Area Here */}
          <WorkspaceArea
            onWorkspaceWidthChange={workAreaWidthHandler}
            sizes={workspaceSize}
          />
        </Col>
        <Col
          id="WorkbookPanel"
          md={workbookWidth}
          sm={12}
          className={workbookClass}
        >
          {/** Start Workbook Area Here */}
          <WorkbookArea
            onWorkbookWidthChange={workAreaWidthHandler}
            sizes={workbookSize}
          />
        </Col>
      </Row>
    </Container>
  );
};
export default ContentArea;
