import React from "react";
import { FaIcon, FacetIcon } from "../graphics/FontAwesomeIcons";
import { InputGroup, DropdownButton, Dropdown, Form } from "react-bootstrap";
import WorkbookIconList from "./WorkbookIconList";

/* This will be replaced with a list that comes from an API call */
const workbooksList = [
  { name: "Workbook One", value: "1" },
  { name: "Workbook Two", value: "2" },
  { name: "Workbook Three", value: "3" },
];
/* this builds the workspace menu list */
const workbookMenuList = [
  { name: "Add a New Workbook", href: "#", iconIndex: 0 },
  { name: "Edit Workbook Properties", href: "#", iconIndex: 1 },
  { name: "Manage Workspaces", href: "#", iconIndex: 2 },
  { name: "Manage Workbooks", href: "#", iconIndex: 3 },
  { name: "Clone a Workbook", href: "#", iconIndex: 4 },
  { name: "Add New Workbook Item", href: "#", iconIndex: 5 },
  { name: "Clone Selected Workbook Items", href: "#", iconIndex: 6 },
  { name: "Delete Selected Workbook Items", href: "#", iconIndex: 7 },
  { name: "Upload Document to Workbook", href: "#", iconIndex: 8 },
  { name: "Download Current Workbook", href: "#", iconIndex: 9 },
];
/**
 * @function WorkspaceMenu
 * @param {*} props brings in properties from parent components
 * @returns Workspace main menu
 */
const WorkspaceMenu = (props) => {
  return (
    <InputGroup className="">
      <Form.Select
        id="workspaceSelection"
        size="sm"
        aria-label="Default select example"
      >
        {workbooksList.map((item, index) => {
          return (
            <option value={index + 1} key={index}>
              {item.name}
            </option>
          );
        })}
      </Form.Select>

      <DropdownButton
        variant="outline-secondary nocaret"
        title={FaIcon("fas", "ellipsis-h", "Workbook Options Menu")}
        id="workspace-main-dropdown"
        align="end"
        onHover={() => {
          alert("hovered");
        }}
      >
        {workbookMenuList.map((item, index) => {
          return (
            <Dropdown.Item eventKey={index + 1} href="#" key={index}>
              <FacetIcon
                iconIndex={item.iconIndex}
                iconList={WorkbookIconList}
              />{" "}
              {item.name}
            </Dropdown.Item>
          );
        })}
      </DropdownButton>
    </InputGroup>
  );
};
export default WorkspaceMenu;
