import React from "react";
import { FaIcon } from "../graphics/FontAwesomeIcons";
import { ButtonGroup, DropdownButton, Dropdown } from "react-bootstrap";

const mainMenuList = [
  { name: "Workspace", iconFam: "fas", iconName: "address-book", onClick: "" },
  { name: "Manage Data", iconFam: "fas", iconName: "database", onClick: "" },
  { name: "Logs", iconFam: "fas", iconName: "clipboard-list", onClick: "" },
  { name: "Statistics", iconFam: "fas", iconName: "chart-bar", onClick: "" },
  {
    name: "User Administration",
    iconFam: "fas",
    iconName: "users-cog",
    link: "",
  },
];

const MainMenu = (props) => {
  return (
    <DropdownButton
      variant="outline-secondary"
      drop="end"
      className="nocaret"
      as={ButtonGroup}
      title={FaIcon("fas", "bars", "FACET Main Menu")}
      id="bg-nested-dropdown"
    >
      {mainMenuList.map((item, index) => {
        return (
          <Dropdown.Item
            eventKey={index + 1}
            key={index}
            onClick={item.onClick}
          >
            {FaIcon(item.iconFam, item.iconName, item.name)} {item.name}
          </Dropdown.Item>
        );
      })}
    </DropdownButton>
  );
};
export default MainMenu;
