import React from "react";
import { Figure } from "react-bootstrap";
import { FacetIcon } from "../graphics/FontAwesomeIcons";
import DocumentIconList from "./DocumentIconList";
/**
 * @function DocumentIcon
 * @param {*} props props.extension to show the icon for that extension
 * @returns Formatted document icon based on the extension being passed
 */
const DocumentIcon = (props) => {
  // Set the IconIndex to zero in case there is no matching file extension
  let IconIndex = 0;
  // Loop through the icons to find one that matches the extension
  for (let i = 0; i < DocumentIconList.length; i++) {
    if (
      DocumentIconList[i].extensions.indexOf(props.extension.toLowerCase()) !==
      -1
    ) {
      IconIndex = i; // Return the index of the icon with the matching extension
    }
  }
  // Return the icon to the page
  return (
    <Figure className="text-center">
      <FacetIcon
        iconIndex={IconIndex}
        iconList={DocumentIconList}
        title={DocumentIconList[IconIndex].name}
      />
      <Figure.Caption>
        {DocumentIconList[IconIndex].longName.length > 0 ? (
          <abbr title={DocumentIconList[IconIndex].longName}>
            {DocumentIconList[IconIndex].name}
          </abbr>
        ) : (
          DocumentIconList[IconIndex].name
        )}
      </Figure.Caption>
    </Figure>
  );
};
export default DocumentIcon;
