import React from "react";
import { Breadcrumb } from "react-bootstrap";

const FileBreadcrumb = (props) => {
  const fullPathParts = props.path.split("/").filter((part) => part !== "");
  return (
    <div className="text-muted font-weight-light small">
      <Breadcrumb bsPrefix="breadcrumb" className="breadcrumb">
        <label className="breadcrumb">Folder: // </label> &nbsp;
        {fullPathParts.map((item, index) => {
          return (
            <Breadcrumb.Item key={index} active>
              {item}
            </Breadcrumb.Item>
          );
        })}
      </Breadcrumb>
    </div>
  );
};
export default FileBreadcrumb;
