import React from "react";
import { FaIcon, FacetIcon } from "../graphics/FontAwesomeIcons";
import { DropdownButton, Dropdown } from "react-bootstrap";
import WorkbookIconList from "./WorkbookIconList";
import ArrayIndexMove from "../../utils/ArrayFunctions";

const workbookItemMenuList = [
  { name: "View Workbook Item", href: "#", iconIndex: 9, className: "" },
  {
    name: "Clone Workbook Item",
    href: "#",
    iconIndex: 12,
    className: "",
  },
  {
    name: "Move Workbook Item",
    href: "#",
    iconIndex: 13,
    className: "",
  },
  { name: "Delete Workbook Item", href: "#", iconIndex: 14, className: "" },
  { name: "Download Workbook Item", href: "#", iconIndex: 15, className: "" },
];
/**
 * @function searchItemMenuPropsList Builds the flag, favorite, and hidden menu item
 * based on the properties passed to the function
 * @param {*} props The properties of flagged, favorite, and hidden are passed
 * @returns
 */
const workbookItemMenuPropsList = (props) => {
  // Build the flag/unflag menu
  const documentMenuObj = {
    name:
      props.document === "true" || props.document === true
        ? "Annotate Document"
        : "Edit Document",
    href: "#",
    iconIndex: props.document === true ? 11 : 10,
    className: "",
  };

  // Return array of data to build dropdowns for falgging, favorite, and hidden
  return [documentMenuObj];
};

const WorkbookItemMenu = (props) => {
  const workbookItemMenus = ArrayIndexMove(
    [...workbookItemMenuList, ...workbookItemMenuPropsList(props)],
    5,
    1
  );
  return (
    <DropdownButton
      variant="outline-secondary nocaret"
      title={FaIcon("fas", "ellipsis-h", "Search Item Options Menu")}
      id="searchOptionsMenu"
      align="end"
    >
      {/** Build the New combined array by adding a spread and combining into new array */}
      {workbookItemMenus.map((item, index) => {
        return (
          <Dropdown.Item
            href={item.href}
            eventKey={index + 1}
            className={item.className}
            key={index}
          >
            <FacetIcon iconIndex={item.iconIndex} iconList={WorkbookIconList} />{" "}
            {item.name}
          </Dropdown.Item>
        );
      })}
    </DropdownButton>
  );
};
export default WorkbookItemMenu;
